.nn {
  border: none;
  /* color: white; */
  border-radius: 10px;
  text-align: center;
  justify-content: center;
  align-self: center;
  align-content: center;
  align-items: center;
  vertical-align: middle;
  /* color: aquamarine !important; */
  justify-items: center;

  background-color: #c0efff;
  font-weight: 700;
  padding: 10px 3px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1.5rem;
  margin: 4px 2px;
  cursor: pointer;
}
.Title {
  font-weight: bold;
  margin-top: 10px;
  /* margin: 10px 5px; */
  margin-bottom: 10px;
  margin-left: 5px;
  margin-right: 5px;
  color: #010326;
  padding: 3px;
  font-size: 3em;
}

.App-particles__container {
  overflow: hidden;
  position: absolute;
  top: 50px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  z-index: 0;
}
.AboutUsContainer {
  height: 100vh;
  padding: 20px;
  text-align: justify;
}
.ECard {
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  text-align: center;
}

.overview {
  padding: 13px;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  max-height: 100%;
  transform: translateY(100%);
  overflow-y: auto;
  transition: transform 0.3s ease-in;
  text-align: justify;
  justify-content: center;
  align-items: center;
}

.ECard:hover .cardtext {
  visibility: hidden;
}

.ECard:hover .overview {
  transform: translateY(0);
  border-radius: 5px;
  height: 100%;
  opacity: 0.8;
}

