.Header .kk {
  justify-content: center;
  align-self: center;
  /* flex-direction: column; */
  font-weight: 600;
  /* font-size:xx-large; */
  text-align: center;
  display: flex;
  flex-direction: column;
  font-size: 3em;
  /* text-align: center; */
  font-family: "Bebas Neue", sans-serif;
}

.Header {
  /* top: 0% !important ; */
  /* width: 100vw; */
  height: auto;
  display: flex;
  background-color: #03318c;
  align-items: center;
  justify-content: center;
  padding: 25px 20px;
  color: white;
  text-align: center;
  /* margin-top: 20px; */
}
.Header h1 {
  text-align: center;
}
.Header img {
  margin-left: auto;
  height: 80px;
  object-fit: cover;
}
.Header button {
  display: none;
}
.Header ul {
  display: flex;
  list-style: none;
}
.Header ul li {
  margin-left: 60px;
}
.Header a {
  text-decoration: none;
  color: #000;
  font-size: 16px;
  font-weight: 500;
}
.Header a:hover {
  color: #564ed3;
}

@media screen and (max-width: 800px) and (min-width: 400px) {
  .Header .kk {
    justify-content: center;
    align-self: center;
    /* flex-direction: column; */
    font-weight: 600;
    /* font-size:xx-large; */
    text-align: center;
    display: flex;
    flex-direction: column;
    font-size: 2em;
    /* text-align: center; */
    font-family: "Bebas Neue", sans-serif;
  }
  .Header {
    /* width: -webkit-fill-available; */
    /* width: 100vw; */
    padding: 0 15px 0 15px;
    /* height: 100px; */
  }
  .Header button {
    display: block;
    outline: none;
    border: none;
    background-color: transparent;
    font-size: 24px;
    z-index: 3;
  }
  /* .Header .kk {
    justify-content: center;
    align-self: center;
    flex-direction: column;
    font-weight: 600;
    font-size: 2em;
    text-align: center;
  } */
  .Header img {
    margin-left: auto;
    height: 60px;
    object-fit: cover;
  }
  .Header ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    width: 300px;
    height: 100%;
    right: -300px;
    top: 0;
    background-color: rgb(223, 223, 223);
    z-index: 2;
    transition: 0.5s;
  }
  .Header ul.active {
    right: 0;
  }
  .Header ul li {
    margin: 20px 0;
  }
  .Header ul li a {
    font-size: 24px;
  }
}

